import { Injectable } from '@angular/core';
import Parse from 'parse';
import { env } from '../../app/environment';

@Injectable()
export class ParseProvider {

  constructor() {
    console.log('Hello ParseProvider Provider');

    this.parseInitialize()
  }

  private parseInitialize() {
    Parse.initialize(env.APP_ID, env.MASTER_KEY);
    Parse.serverURL = env.SERVER_URL;
  }

  public getTypeOfBank() {
    const PK = Parse.Object.extend('PendanaanKoperasi');
    let q = new Parse.Query(PK);
        q.ascending('sort');
        q.equalTo('status', true)
    return q.find()
  }

  public pushData(data): Promise<any> {
    return new Promise((resolve, reject) => {
      const PendanaanKoperasiCustomers = Parse.Object.extend('PendanaanKoperasiCustomers')
      let item = new PendanaanKoperasiCustomers();
          item.set('name', data.nama);
          item.set('company', data.perusahaan);
          item.set('position', data.jabatan);
          item.set('phone_number', data.noHp);
          item.set('telephone_number', data.noTelepon);
          item.set('number_of_employees', data.jumlahKaryawanTetap);
          item.set('freelance', parseInt(data.karyawanLepas));
          item.set('average_salary', parseInt(data.rataGaji));
          item.set('limit', parseInt(data.rencanaRataPlafon));
          item.set('email', data.email);
          item.set('agreement', data.agreement);
          item.set('bank', data.bank);
          item.save().then((success) => {
            resolve(success);
          }, (error) => {
            reject(error);
          });
    })
  }
}



